export default {
    path: '/report',
    component: ()=>import('../../views/report'),
    children: [
        {
            path: 'enterprise',
            name: 'enterprise',
            component: ()=>import('../../components/enterpriseReport')
        },
        {
            path: 'individual',
            component: ()=>import('../../components/individualReport')
        },
    ]
}


