export default {
    path: '/rulesCreated',
    component: ()=>import('../../views/rulesCreated'),
    children: [
        {
            path: '',
            redirect: 'enterpriseRulesCreated'
        },
        {
            path: 'enterpriseRulesCreated',
            component: ()=>import('../../components/enterpriseRulesCreated')
        },
        {
            path: 'individualRulesCreated',
            component: ()=>import('../../components/individualRulesCreated')
        },
        // {
        //     path: 'generateRules',
        //     component: () => import('../../components/generateRules')
        // }
    ],
}



