export default {
    path: '/check',
    component: ()=>import('../../views/check'),
    children: [
        {
            path: '',
            redirect: 'enterprise'
        },
        {
            path: 'enterprise',
            component: ()=>import('../../components/enterpriseCheck')
        },
        {
            path: 'individual',
            component: () => import('../../components/individualCheck')
        },

    ],
}



