import Vue from 'vue'
import Router from 'vue-router'
import indexRouter from './entry'
import registerRouter from './register'
import managesRouter from './manages'
import rulesRouter from './rulesCreated'
import checkRouter from './check'
import reportRouter from './report'
import checkListRouter from './checkList'

Vue.use(Router)

var VueRouter = new Router({
  // mode: 'history',
  // base: process.env.BABEL_URL,
  routes: [
      indexRouter,
      registerRouter,
      managesRouter,
      rulesRouter,
      checkRouter,
      checkListRouter,
      reportRouter,
      {
          path: '/*',
          redirect: '/index'
      }
  ]

})


// 挂载路由导航守卫
VueRouter.beforeEach((to, from, next) => {
  // to 将要访问的路径
  // from 代表从哪个路径跳转过来
  // next 是一个函数  表示放行
  // next() 表示放行
  // next('/') 要强制跳转的路径

    // 注释

  let nextPath='/register,/index'
  if (nextPath.indexOf(to.path)>-1) return next()
  // 获取token
  const tokenStr = window.sessionStorage.getItem('user')
  if (!tokenStr) return next('/index')
  next()
})

export default VueRouter






